var syllable = require('syllable');

var Tokenizer = require('sentence-tokenizer');

exports.readingLevel = function (text, full) {
  var tokenizer = new Tokenizer('ChuckNorris');
  tokenizer.setEntry(text);
  var tokenSentences = tokenizer.getSentences();
  var tracker = {
    syllables: 0,
    words: 0
  };
  var counts = tokenSentences.reduce(function (obj, sentence) {
    // strip all punctuation and numbers from the sentences
    var words = sentence.replace(/[^\w\s]|_/g, "").replace(/\s+/g, " ").replace(/[0-9]/g, '').split(' ').filter(function (letter) {
      return letter;
    });
    obj.words += words.length; // count up all the syllables

    obj.syllables += words.reduce(function (total, word) {
      return total += syllable(word);
    }, 0);
    return obj;
  }, tracker);
  var words = counts.words,
      syllables = counts.syllables;
  var sentences = tokenSentences.length;
  var first = words / sentences;
  var second = syllables / words;
  var unrounded = 0.39 * first + 11.8 * second - 15.59;
  var result = {
    sentences: sentences,
    words: words,
    syllables: syllables,
    unrounded: unrounded
  };
  result.rounded = Math.round(isNaN(unrounded) ? NaN : unrounded);
  var err = 'Either no sentences or words, please enter valid text';

  if (isNaN(result.rounded)) {
    result.error = err;
  }

  if (full == 'full') {
    return result;
  }

  if (isNaN(result.rounded)) {
    return err;
  }

  return result.rounded;
};